<template>
  <div class="mx-auto">
    <div class="main-wrapper grid md:grid-cols-2 sm:grid-cols-1 gap-4">
      <div id="3dmain"></div>
      <div class="flex items-center">
        <ul>
          <li class="text-5xl font-semibold"><h2>Hi! I'm Tuan</h2></li>
          <li class="text-3xl">
            <pre>I'm a Developer . Nice to meet you</pre>
          </li>
          <li>
            <button
              class="px-4 py-2 bg-orange-400 hover:bg-orange-500 shadow-lg rounded-full text-xl font-thin"
              @click="scrollToView('aboutme')"
            >
              About me
            </button>
          </li>
        </ul>
      </div>
    </div>
    <div id="aboutme" class="about-wrapper grid md:grid-cols-2 sm:grid-cols-1 gap-4">
        <div>Content</div>
        <div>Image</div>
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
export default {
  name: "HomePage",

  data() {
    return {};
  },

  async mounted() {
    const container = document.getElementById("3dmain");
    let camera = new THREE.PerspectiveCamera(
      50,
      container.clientWidth / container.clientHeight,
      0.1,
      1000
    );
    camera.position.set(-126, 126, 102);

    let renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
    });
    renderer.setPixelRatio(devicePixelRatio);
    renderer.setSize(container.clientWidth, container.clientHeight);
    renderer.gammaOuput = true;
    renderer.gammaFactor = 2.2;
    let scene = new THREE.Scene();
    const loader = new GLTFLoader();
    let model;
    loader.load(
      "/assets/models/scene.gltf",
      (gltf) => {
        model = gltf.scene;
        scene.add(model);
      },
      (xhr) => {
        console.log(xhr);
      },
      (error) => console.log(error)
    );

    var light = new THREE.AmbientLight(0xffffff);
    scene.add(light);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.autoRotate = true;
    container.appendChild(renderer.domElement);
    renderer.setAnimationLoop(() => {
      onWindowResize();
      controls.update();
      renderer.render(scene, camera);
    });

    function onWindowResize() {
      camera.aspect = container.clientWidth / container.clientHeight;

      camera.updateProjectionMatrix();

      renderer.setSize(container.clientWidth, container.clientHeight);
    }
  },

  methods: {
    scrollToView(view){
      const el = document.getElementById(view);
      el.scrollIntoView({behavior: "smooth"});
    }
  },
};
</script>

<style lang="css" scoped>
.main-wrapper {
  height: 100vh;
  background-image: url("/images/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-position: center center;
}
ul li {
  margin-top: 8px;
}

.about-wrapper{
  height: 100vh;
}
</style>
